<template>
  <v-row>
    <v-col sm="12">
      <base-card>
<!--        <v-card-title>-->
<!--           -->
<!--          <v-btn color="primary" class="ml-4" to="settings/add_account"></v-btn>-->
<!--        </v-card-title>-->
        <v-card-text>
            <v-data-table
                :headers="headers"
                :items="priceDrivers"
                hide-default-footer
                disable-sort
                no-data-text="  "
            >
              <template v-slot:top>
                <v-toolbar
                    flat
                >
                  <v-toolbar-title> </v-toolbar-title>
                  <v-divider
                      class="mx-4"
                      inset
                      vertical
                  ></v-divider>
                  <!--                <v-spacer></v-spacer>-->
<!--                  <v-btn color="primary" to="settings/add_account"></v-btn>-->
                  <v-dialog
                      v-model="addDialog"
                      max-width="600px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                          color="primary"
                          v-bind="attrs"
                          v-on="on"
                      ></v-btn>
                    </template>

                    <v-card>
                      <v-card-title>
                        <span class="text-h5"> </span>
                      </v-card-title>

                      <v-card-text class="pt-4">
                        <v-form
                            ref="AccountForm"
                            lazy-validation
                        >
                          <v-text-field
                              v-model="AccountName"
                              label=""
                              :rules="[v => !!v || ' ']"
                              required
                              outlined
                          />
                          <v-row>
                            <v-col md="6">
                              <v-select
                                  v-model="AccountPlatform"
                                  :items="AccountPlatforms"
                                  placeholder=" "
                                  required
                                  outlined
                              />
                            </v-col>
                            <v-col md="6">
                              <v-select
                                  v-model="AccountType"
                                  placeholder=" "
                                  :items="AccountTypes"
                                  return-object
                                  required
                                  outlined
                              />
                            </v-col>
                          </v-row>
                          <v-checkbox
                              v-model="Simulated"
                              label="Simulated"
                          />
                          <template v-if="Simulated === false">
                            <v-text-field
                                v-model="AccountPub"
                                label="Public Key"
                                :rules="[v => !!v || '     !']"
                                required
                                outlined
                            />
                            <v-text-field
                                v-model="AccountPriv"
                                label="Private Key"
                                :rules="[v => !!v || '     !']"
                                required
                                outlined
                            />
                          </template>
                          <v-btn color="success" class="my-4" @click="addAccount" :disabled="bussy"></v-btn>
                        </v-form>
                      </v-card-text>
                    </v-card>
                  </v-dialog>
                </v-toolbar>
              </template>
              <template v-slot:item.Status="{item}">
                <template v-if="item.Status === 'true'">
                  <v-chip
                      class=""
                      color="success"
                      label
                      small
                      text-color="white"
                  >
                    <v-icon small left >mdi-check</v-icon>
                    
                  </v-chip>
                </template>
                <template v-else>
                  <v-chip
                      class=""
                      color="info"
                      label
                      small
                      text-color="white"
                  >
                    <v-progress-circular
                        indeterminate
                        :value="100"
                        color="white"
                        :size="15"
                        :width="2"
                        class="mr-2"
                    ></v-progress-circular>
                    
                  </v-chip>
                </template>
              </template>

              <template v-slot:item.actions="{item}">
                <v-btn color="danger" small text @click="removeAccount(item.id)"></v-btn>
              </template>
            </v-data-table>
        </v-card-text>
      </base-card>
    </v-col>
  </v-row>
</template>

<script>
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";

export default {
  data() {
    return {
      addDialog: false,
      bussy: false,
      headers: [
        { text: "", value: "AccountName" },
        { text: "", value: "AccountPlatform" },
        { text: "", value: "AccountType" },
        { text: "", value: "Status", align: "center", width: "1%" },
        { text: "", value: "actions", align: "center", width: "1%" }
      ],
      priceDrivers: [],
      AccountPlatforms: [
        'Binance',
        'Bybit'
      ],
      AccountTypes: [
        { text: '', value: 'Spot Trading' },
        { text: '', value: 'Leverage Trading' }
      ],
      AccountName: '',
      AccountPlatform: '',
      AccountType: { text: '', value: 'spot' },
      AccountPriv: '',
      AccountPub: '',
      Simulated: false
    }
  },
  created() {
    var db = firebase.firestore();
    firebase.auth().onAuthStateChanged(async user => {
      var priceDrivers = db.collection('users').doc(user.uid).collection('PriceDrivers');
      priceDrivers.onSnapshot(snapshot => {
        this.priceDrivers = [];

        snapshot.forEach(doc => {
          this.priceDrivers.push({
            id: doc.id,
            AccountName: doc.data()['AccountName'],
            AccountPlatform: doc.data()['AccountPlatform'],
            AccountType: doc.data()['AccountType'],
            Status: doc.data()['Status']
          });
        });
      });
    });
  },
  methods: {
    removeAccount(id) {
      var db = firebase.firestore();

      firebase.auth().onAuthStateChanged(async user => {
        var priceDrivers = db.collection('users').doc(user.uid).collection('PriceDrivers');

        var realdb = firebase.database();
        await realdb.ref('tasks').push().set({
          task: 'delete_account',
          user: user.uid,
          data: {
            account_id: id
          }
        });

        // priceDrivers.doc(id).delete();
      });
    },
    addAccount() {
      if(!this.$refs.AccountForm.validate()) return false;
      if(!this.bussy) {
        this.bussy = true;
      }
      else if(this.bussy === true) {
        return false;
      }

      var db = firebase.firestore();
      var realdb = firebase.database();
      firebase.auth().onAuthStateChanged(async user => {
        await db.collection('users').doc(user.uid).collection('PriceDrivers').add({
          AccountName: this.AccountName,
          AccountPlatform: this.AccountPlatform,
          AccountType: this.AccountType.value,
          AccountPub: this.AccountPub,
          AccountPriv: this.AccountPriv,
          Simulated: this.Simulated
        }).then(async data => {
          await realdb.ref('tasks').push().set({
            task: "add_account",
            user: user.uid,
            data: {
              PriceDriver: data.id,
              AccountType: this.AccountType.value,
              AccountPub: this.AccountPub,
              AccountPriv: this.AccountPriv,
              Simulated: this.Simulated
            }
          });
          this.addDialog = false;
          this.bussy = false;
        }).catch(data => {
          /* Error if can't add the Account */
        });

      });

      // this.$router.push('/settings');
    }
  }
}
</script>